import React from "react";
import ModuleLayout from "./ModuleLayout";

const ModulePage = ({ params }: { params: { id: string } }) => {
  const moduleId = parseInt(params.id, 10);

  // Datos ficticios para el módulo
  const moduleData = {
    title: "Getting Started: The English Alphabet and Numbers",
    subtitle: "Introducción a letras, números básicos y fonética sencilla",
    videoUrl: "https://www.youtube.com/embed/dQw4w9WgXcQ", // Ejemplo de video
    description: `
      Lorem ipsum dolor sit amet consectetur. Lectus proin eget libero enim ullamcorper. 
      Quam sit morbi fermentum eu sed placerat parturient id molestie.
    `,
    resources: [
      { title: "Nombre del taller 1", link: "/resources/1" },
      { title: "Nombre del taller 2", link: "/resources/2" },
    ],
  };

  const handleExamClick = () => {
    alert(`¡Presentar examen del módulo ${moduleId}!`);
  };

  return (
    <ModuleLayout
      title={moduleData.title}
      subtitle={moduleData.subtitle}
      videoUrl={moduleData.videoUrl}
      description={moduleData.description}
      resources={moduleData.resources}
      onExamClick={handleExamClick}
    />
  );
};

export default ModulePage;
